<template>
  <div class="p-comments__content">
    <section v-if="errored" class="p-error__wrap">
      <p class="u-font__sub">
      
      </p>
    </section>

    <section v-else class="p-comments__list">
      <div v-if="loading" class="u-font__sub">Now Loading...</div>
      <div v-else class="p-comments__wrap">
        <div class="c-comment" v-if="publicmsgs">
          <p class="u-font__s">{{ publicmsgs.length }}</p>
           <div v-if="publicmsgs.length > 5 && !getAll">
            <p class="u-font__s">4</p>
            <p @click="getMsg" class="c-btn__moderate"></p>
              <div class="c-comment__wrap" v-for="msg in publicmsgs.slice(-4, publicmsgs.length)" >
                <div class="c-comment__header">
                  <span class="c-comment__info">
                    From:
                    <a v-if="msg.user.name" class="c-comment__link" :href="'/projects/'+msg.user.id+'/profile'" >
                      {{ msg.user.name }}
                    </a>
                    <span v-else ></span>
                  </span>
                </div>
                <div class="c-comment__body u-indention">{{ msg.content }}</div>
                <div class="c-comment__footer">
                  <span class="c-comment__info" >{{ msg.send_date }}</span>
                </div>
              </div>
           </div>
           <div v-else>
            <p class="u-font__s"></p>
            <p v-if="publicmsgs.length > 5" @click="closeMsg" class="c-btn__moderate"></p>
             <div class="c-comment__wrap" v-for="msg in publicmsgs" >
               <div class="c-comment__header">
                 <span class="c-comment__info">
                   From:
                   <a v-if="msg.user.name" class="c-comment__link" :href="'/projects/'+msg.user.id+'/profile'" >
                     {{ msg.user.name }}
                   </a>
                   <span v-else ></span>
                 </span>
               </div>
               <div class="c-comment__body u-indention">{{ msg.content }}</div>
               <div class="c-comment__footer">
                 <span class="c-comment__info" >{{ msg.send_date }}</span>
               </div>
             </div>
           </div>
        </div>
        <p v-else class="u-font__sub">
        
        </p>
      </div>
    </section>
  </div>
</template>

<script>
  export default {
    props:['project'],
    data () {
      return {
        publicmsgs: [], //json
        loading: true,
        errored: false,
        getAll: false,
      }
    },
    methods: {
      getMsg(){
        this.getAll = true;
      },
      closeMsg(){
        this.getAll = false;
      }
    },
    mounted () {
      //json
      var self = this;
      var url = '/projects/' + self.project.id + '/msg_json'; //data
      axios
        .get(url)
        .then(response => (self.publicmsgs = response.data))
        .catch(function (error) {
          console.log(error);
          self.errored = true
        })
        .finally(
          () => self.loading = false
          )
    }
  }
</script>
